form {
    input {
        height: 32px;
        @media (min-width: $m) {
            height: 46px; } }
    textarea {
        height: 171px;
        resize: none;
        @media (min-width: $m) {
            height: 232px; } }
    input,
    textarea {
        font-family: $font-medium;
        line-height: 25px;
        border: 1px solid $form-border;
        margin: 10px 0;

        padding: 10px 14px;
        @media (min-width: $m) {
            font-size: 18px; }
        &::placeholder {
            font-family: $font;
            font-weight: 300;
            @media (min-width: $m) {
                font-size: 18px; } }
        &:focus {
            outline-color: $hover-blue; } }
    &.submitted {
        input,
        textarea {
            &:invalid {
                border-color: $error-red;
                &:focus {
                    outline-color: $error-red; } } } } }
